.App {
  text-align: center;
  padding: 20px;
}

h1, h2 {
  color: #007bff;
}

.App-header {
  background-color: #282c34;
  padding: 20px;
  color: white;
}

nav ul {
  list-style: none;
  padding: 0;
  display: flex;
  justify-content: center;
}

nav li {
  margin: 0 15px;
}

nav a {
  color: #61dafb;
  text-decoration: none;
}

nav a:hover {
  text-decoration: underline;
}
.progress-bar {
  height: 10px;
  background-color: #e0e0e0;
  border-radius: 5px;
  overflow: hidden;
  margin-bottom: 20px;
}

.progress-bar div {
  height: 100%;
  background-color: #007bff;
  width: 0;
  transition: width 0.3s;
}
