.onboarding-container {
    max-width: 600px;
    margin: auto;
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 8px;
  }
  
  h2 {
    text-align: center;
    margin-bottom: 20px;
  }
  
  .form-group {
    margin-bottom: 15px;
  }
  
  label {
    display: block;
    margin-bottom: 5px;
  }
  
  input, select, textarea {
    width: 100%;
    padding: 10px;
    border-radius: 4px;
    border: 1px solid #ccc;
  }
  
  textarea {
    height: 100px;
  }
  
  .selected-items {
    display: flex;
    flex-wrap: wrap;
    margin-top: 10px;
  }
  
  .selected-items span {
    background-color: #007bff;
    color: white;
    padding: 5px;
    border-radius: 4px;
    margin-right: 5px;
    cursor: pointer;
  }
  
  .form-navigation {
    display: flex;
    justify-content: space-between;
  }
  
  button {
    padding: 10px 20px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  button:hover {
    background-color: #0056b3;
  }
  