.offering-analysis {
    text-align: left;
    margin: 20px;
    padding: 20px;
    border: 1px solid #ddd;
    background-color: #fff;
    border-radius: 8px;
  }
  
  .offering-analysis h2, .offering-analysis h3, .offering-analysis h4 {
    color: #007bff;
  }
  
  .offering-analysis ul {
    list-style-type: none;
    padding-left: 0;
  }
  
  .offering-analysis li {
    margin-bottom: 10px;
  }
  