.sales-pipeline-container {
    max-width: 1000px;
    margin: auto;
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 8px;
  }
  
  h2 {
    text-align: center;
    margin-bottom: 20px;
  }
  
  .pipeline {
    display: flex;
    justify-content: space-between;
  }
  
  .pipeline-stage {
    background-color: #e0e0e0;
    padding: 10px;
    border-radius: 8px;
    width: 18%;
    min-height: 200px;
  }
  
  .pipeline-stage h3 {
    text-align: center;
    margin-bottom: 10px;
  }
  
  .lead-card {
    background-color: #fff;
    margin-bottom: 10px;
    padding: 10px;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    cursor: grab;
  }
  
  .lead-card p {
    margin: 0;
  }
  
  .lead-card button {
    margin-top: 10px;
    padding: 5px 10px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .lead-card button:hover {
    background-color: #0056b3;
  }
  