.dashboard-container {
  max-width: 1200px;
  margin: auto;
  padding: 20px;
  background-color: #f9f9f9;
  border-radius: 8px;
}

h2 {
  text-align: center;
  margin-bottom: 20px;
}

.filters {
  display: flex;
  gap: 15px;
  margin-bottom: 20px;
}

.filters select {
  padding: 10px;
  border-radius: 4px;
  border: 1px solid #ccc;
}

.lead-table {
  margin-bottom: 20px;
}

.lead-table table {
  width: 100%;
  border-collapse: collapse;
}

.lead-table th, .lead-table td {
  border: 1px solid #ccc;
  padding: 10px;
  text-align: left;
}

.data-insights, .real-time-alerts {
  margin-bottom: 20px;
}

.report, .alert {
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 10px;
  margin-bottom: 10px;
}
