.interactive-elements {
    margin: 20px 0;
    padding: 10px;
    border: 1px solid #ddd;
    background-color: #fff;
}

.interactive-elements button {
    padding: 10px 20px;
    font-size: 16px;
    color: #fff;
    background-color: #007bff;
    border: none;
    cursor: pointer;
    border-radius: 5px;
}

.interactive-elements button:hover {
    background-color: #0056b3;
}
