.transaction-dashboard-container {
    max-width: 1000px;
    margin: auto;
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 8px;
  }
  
  h2 {
    text-align: center;
    margin-bottom: 20px;
  }
  
  .metrics {
    display: flex;
    justify-content: space-around;
    margin-bottom: 20px;
  }
  
  .transaction-history {
    margin-bottom: 20px;
  }
  
  .transaction-history table {
    width: 100%;
    border-collapse: collapse;
  }
  
  .transaction-history th, .transaction-history td {
    border: 1px solid #ccc;
    padding: 10px;
    text-align: left;
  }
  
  button {
    padding: 10px 20px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  button:hover {
    background-color: #0056b3;
  }
  