.vendor-details {
    border: 1px solid #ddd;
    padding: 20px;
    margin-top: 20px;
    background-color: #fff;
    border-radius: 8px;
}

.vendor-details h2 {
    color: #007bff;
}

.vendor-details .vendor-contact p {
    margin: 5px 0;
}

.vendor-details .vendor-stats {
    margin-top: 20px;
}

.vendor-details .vendor-stats h3 {
    color: #333;
}

.vendor-details .vendor-stats p {
    margin: 5px 0;
}
