.ai-matching-container {
    max-width: 800px;
    margin: auto;
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 8px;
  }
  
  h2 {
    text-align: center;
    margin-bottom: 20px;
  }
  
  .matches-list {
    display: flex;
    flex-wrap: wrap;
    gap: 15px;
  }
  
  .match-card {
    background-color: #fff;
    border: 1px solid #ccc;
    border-radius: 8px;
    padding: 15px;
    width: calc(33.333% - 10px);
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  }
  
  .profile-preview h3 {
    margin-bottom: 5px;
  }
  
  .engagement-buttons button {
    margin-right: 10px;
    padding: 8px 12px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .engagement-buttons button:hover {
    background-color: #0056b3;
  }
  
  .feedback {
    margin-top: 10px;
  }
  
  .feedback button {
    background: none;
    border: none;
    cursor: pointer;
    font-size: 18px;
    margin-left: 5px;
  }
  